    function ejSideMenu() {
        let menuOptions
        let menu = document.querySelector('#sideMenu01-options')
        menuOptions = [...menu.children]
        
        for (let i = 0; i < menuOptions.length; i++) {
            menuOptions[i].onclick = clickCallback          
        }

        function clickCallback(event) {
            for (let i = 0; i < menuOptions.length; i++) {
                menuOptions[i].classList.remove("sideMenu--option__selected")                
            }
            this.classList.add("sideMenu--option__selected")
        }
    }
    ejSideMenu()

